* {
	box-sizing: border-box;
	font-family: Raleway, sans-serif;
}

body {
	margin: 0;
	background-color: #121212;
}

.navbar {
	padding-left: 12rem;
	width: 85%;
	background-color: #121212;
	color: white;
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 11vh;
	animation: fadeIn 1s ease-out 0.5s;
	animation-fill-mode: both;
}
svg {
	width: 1em;
	font-size: 2rem;
}
.logo {
	font-size: 1.75rem;
	font-weight: 700;
}
.navbar ul {
	padding: 0;
	margin: 0;
	list-style: none;
	display: flex;
	gap: 5rem;
	font-size: 17px;
	align-items: center;
}
.navbar a {
	color: inherit;
	text-decoration: none;
}
.navbar li:hover {
	transform: scale(1.11);
	transition: 0.3s;
}

@media (max-width: 977px) {
	.navbar {
		width: 90%;
		padding-left: 6rem;
	}
	.navbar ul {
		font-size: 15px;
		gap: 2rem;
	}
	svg {
		width: 0.8em;
	}
}

@media (max-width: 770px) {
	.navbar {
		padding-left: 1rem;
		width: 95%;
	}
}

@keyframes fadeIn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
.body {
	padding-left: 12rem;
	width: 85%;
	color: white;
	height: 100vh;
}
.main-wrapper {
	display: flex;
	padding-top: 4rem;
	align-items: center;
	justify-content: space-between;
}

.title {
	display: block;
	font-size: 6rem;
	font-weight: bold;
	animation: slideUp 0.5s ease 1.5s;
	animation-fill-mode: both;
}
.playful {
	transition: all 0.4s cubic-bezier(0.15, 0.75, 0.45, 0.95) 0s;
	animation: 10s ease 0s infinite alternate none running rainbow;
}

.personicon {
	animation-fill-mode: both;
	width: 230px;
	height: 270px;
	animation: slideUp 0.5s ease 2s;
	animation-fill-mode: both;
}

.subtitle {
	font-size: 1.4rem;
	font-weight: 400;
	animation: slideUp 0.5s ease 2.5s;
	animation-fill-mode: both;
}
.project-button {
	color: white;
	cursor: pointer;
	display: block;
	margin: 2rem auto;
	background-color: #121212;
	font-size: 1rem;
	font-weight: 700;
	border: none;
	outline: none;
	animation: slideUp 0.5s ease 3s;
	animation-fill-mode: both;
}
.project-button svg {
	display: block;
	margin: 0.4rem auto;
	width: 1.1rem;
}

@media (max-width: 1289px) {
	.title {
		font-size: 5rem;
	}
	.personicon {
		width: 180px;
		height: 200px;
	}
	.main-wrapper {
		padding-top: 8rem;
	}
}
@media (max-width: 977px) {
	.body {
		width: 90%;
		padding-left: 6rem;
	}
}
@media (max-width: 770px) {
	.body {
		padding-left: 1rem;
		width: 95%;
	}
}
@media (max-width: 694px) {
	.title {
		font-size: 3rem;
	}
	.main-wrapper {
		padding-top: 12rem;
	}
	.personicon {
		width: 130px;
		height: 150px;
	}
	.subtitle {
		font-size: 1rem;
	}
	.project-button {
		margin: 5rem auto;
		font-size: 0.9rem;
	}
	.project-button svg {
		width: 0.7rem;
	}
}

@media (max-width: 510px) {
	.title {
		font-size: 2rem;
	}
	.personicon {
		width: 100px;
		height: 120px;
	}
	.subtitle {
		font-size: 0.8rem;
	}
}

@keyframes rainbow {
	0% {
		color: rgb(240, 77, 255);
	}
	25% {
		color: rgb(255, 77, 77);
	}
	50% {
		color: rgb(255, 197, 77);
	}
	75% {
		color: rgb(106, 237, 118);
	}
	100% {
		color: rgb(0, 153, 255);
	}
}

@keyframes slideUp {
	0% {
		opacity: 0;
		transform: translateY(60%);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}
